<template>
   <v-container fill-height justify-center fluid>
     <h1>이름입력</h1>
     <v-row>
       <v-col cols="12">
         <v-text-field label="이름" v-model="이름"/>
       </v-col>
       <v-col  cols="12">
         <v-text-field label="닉네임" v-model="닉네임"/>
       </v-col>
     </v-row>
    <v-btn @click="createAccount(이름,닉네임)">저장</v-btn>
  </v-container>
</template>

<script>
// import { onAuthStateChanged } from "firebase/auth";
// import { addDoc, collection,  } from "firebase/firestore";
import * as firestore from "firebase/firestore"
export default {
    name: 'Dashboard',
    data(){
      return{
        이름:"",
        닉네임:"",
      }
    },
    computed:{
    },
    watch:{
      // currentUser(newval){
      //   console.log(newval)
      // }
    },
    created(){
      // onAuthStateChanged(this.auth)
    },
    mounted(){
      // console.log()
    },
    methods:{
      clickButton(){
        console.log(this.$currentUser)
      },
      async createAccount(이름,닉네임){
        try {
          const uid = this.$currentUser.uid
          const payload = {
            이름:이름,
            닉네임:닉네임,
            가입일: new Date(),
            // 나이:29,
            마지막접속: new Date(),
            // 성별: "남",
            // 지역: "성남시",
          }

          const ref = firestore.collection(this.$db, "사용자")
          await firestore.setDoc(firestore.doc(ref,uid), payload);
          this.$router.push("/dashboard")
        } catch (e) {
          console.error("Error adding document: ", e);
        }
      }
    }

}
</script>

